import React from "react";
import { Container, Row, Col } from 'react-bootstrap';
import Spinner from './Spinner.js';

class Detail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  componentDidMount() {
    document.getElementsByClassName("wrapper")[0].classList.remove("menu-open");
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
    window.scrollTo(0, 0);
  }

  render() {
    if(this.props.dataReady){
      var i;
      var data;
      var eventTitle;
      const events = this.props.events;
      for (i = 0; i < this.props.data.length; i++) {
        if(this.props.data[i].title == this.props.match.params.detail){
          data = this.props.data[i];
          eventTitle = events.find(event => event.id === data.event_id) ? events.find(event => event.id === data.event_id).title : '';
        }
      }
      return (
        <div className="detailWp">
          <Container>
            <Row>
              <Col xs={12} xl={6}>
                <CoverImg data={data} />
              </Col>
              <Col xs={12} xl={6}>
                <h2>{data.title}</h2>
                <h5><a href={"#/events/"+eventTitle}>{eventTitle}</a></h5>
                <div className="content">
                {data.content.split('\n').map(i => {
                    return <p>{i}</p>
                })}
                </div>
                <div className="share">
                  <ul className="nav">
                    <li><a href={"https://www.facebook.com/sharer.php?u="+window.location.href} target="_blank"><i className="fa fa-facebook"></i></a></li>
                    <li><a href={"https://twitter.com/share?url="+window.location.href} target="_blank"><i className="fa fa-twitter"></i></a></li>
                    <li><a href={"https://wa.me/?text="+window.location.href} target="_blank"><i className="fa fa-whatsapp"></i></a></li>
                  </ul>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      );
    }
    return (
      <div className="text-center">
        <br />
        <Spinner />
        <br />
      </div>
    )
  }
}

function CoverImg(props) {
  if(props.data.cover_img){
    return (
      <img src={props.data.cover_img} alt={props.data.title} className="w-100 border shadow" />
    )
  }
  return "";
}

export default Detail;
