import React from "react";
import { Link, Route } from "react-router-dom";
import { Container } from 'react-bootstrap';
import Spinner from './Spinner.js';

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  render() {
    return (
      <div className="header">
        <Container>
          <div className="h-1">
            <div className="h-logo">
              <h1><Link to="/">Free HK</Link></h1>
            </div>
            <Menu events={this.props.events} toggleMenu={this.props.toggleMenu} />
            <MenuTg toggleMenu={this.props.toggleMenu} />
          </div>
        </Container>
      </div>
    );
  }
}

function MenuTg(props) {
  return (
    <button className="menu-tg" onClick={props.toggleMenu}></button>
  )
}

function Menu(props) {
  const openSubmenuM = (event) => {
    event.target.classList.toggle("open");
  }
  return (
    <div className="menu">
      <div className="d-xl-none menu-overlap" onClick={props.toggleMenu}></div>
      <ul className="nav">
        <li>
          <Link to="/">Timeline</Link>
        </li>
        <li>
          <Link to="/">Events</Link><button className="submenu-tg" onClick={openSubmenuM}></button>
          <Submenu items={props.events} />
        </li>
        <li>
          <Link to="/about">About</Link>
        </li>
        <li>
          <Link to="/download">Download</Link>
        </li>
      </ul>
    </div>
  )
}

function Submenu(props) {
  if(props.items.length > 0){
    return (
      <div className="submenu">
        <ul className="nav">
        {
          props.items.map(obj => {
            return (
              <li key={obj.id}>
                <a href={"#/events/"+obj.title}>{obj.title}</a>
              </li>
            )
          })
        }
        </ul>
      </div>
    )
  }
  return (
    <div className="submenu text-center p-4">
      <Spinner />
    </div>
  )
}

export default Header;
