import React from "react";
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import { Link } from "react-router-dom";
import Spinner from './Spinner.js';

class Timeline extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      handleEventsDone: false,
      eventHoverWp: false
    };
    this.handleHoverSame = this.handleHoverSame.bind(this);
    this.handleEvents = this.handleEvents.bind(this);
  }

  componentDidMount() {

  }

  handleHoverSame = (event) => {
    if (event) {
      var i;
      for (i = 0; i < this.props.events.length; i++) {
        var arr = [];
        arr[this.props.events[i].title] = event === this.props.events[i].title ? true : false;
        this.setState(arr);
      }
      this.setState({
        eventHoverWp: true,
      });
    } else {
      this.setState({
        eventHoverWp: false,
      });
    }
  }

  handleEvents = (events) => {
    if(this.state.handleEventsDone){
      return false;
    }
    this.setState({
      handleEventsDone: true
    });
    var i;
    for (i = 0; i < events.length; i++) {
      var arr = [];
      arr[events[i].title] = false;
      this.setState(arr);
    }
  }

  render() {
    if (this.props.ready) {
      const events = this.props.events;
      const data = this.props.data;
      const selectEvents = this.props.event ? this.props.event : "";
      this.handleEvents(events);
      return (
        <VerticalTimeline className={this.state.eventHoverWp ? "hovering" : ""}>
          {
            data.map(obj => {
              const eventTitle = events.find(event => event.id === obj.event_id) ? events.find(event => event.id === obj.event_id).title : '';
              if(!selectEvents || (eventTitle && eventTitle === selectEvents)){
                return (
                  <VerticalTimelineElement
                    key={obj.title}
                    className={"vertical-timeline-element--work"+(!this.state.eventHoverWp || this.state[eventTitle] ? " active" : "")}
                    date={obj.date}
                  >
                    <div onMouseEnter={() => this.handleHoverSame(eventTitle)} onMouseLeave={() => this.handleHoverSame("")}>
                      <h3 className="vertical-timeline-element-title"><Link to={"/d/"+obj.title}>{obj.title}</Link></h3>
                      <h6 className="vertical-timeline-element-subtitle"><Link to={"/events/"+eventTitle}>{eventTitle}</Link></h6>
                      <p>
                        {obj.desc}
                      </p>
                    </div>
                  </VerticalTimelineElement>
                )
              }
              return "";
            })
          }
        </VerticalTimeline>
      );
    }
    return (
      <div className="text-center">
        <br />
        <Spinner />
          <br />
      </div>
    )
  }
}

export default Timeline;
