import React from "react";
import Timeline from './Timeline.js';
import { Container } from 'react-bootstrap';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  componentDidMount() {
    document.getElementsByClassName("wrapper")[0].classList.remove("menu-open");
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <Container>

        <Timeline data={this.props.data} event={this.props.match.params.event} events={this.props.events} ready={this.props.dataReady > 0 ? true : false} />

      </Container>
    );
  }
}

export default Home;
